
import React, { useState } from 'react';
import '../css/ProductCard.css';

const ProductCard = ({ product, onAddToCart }) => {
    const [isAdded, setIsAdded] = useState(false);
    const [selectedSize, setSelectedSize] = useState('');
    const [quantity, setQuantity] = useState(1); 
    const [isEnlarged, setIsEnlarged] = useState(false);
    const [showNotification, setShowNotification] = useState(false);
    
    const Notification = ({ message, onClose }) => (
        <div className="notification">
            <p>{message}</p>
            <button onClick={onClose}>Cerrar</button>
        </div>
        );
    const handleAddToCart = () => {
        if (selectedSize) {
            setIsAdded(true);
            // Llama a la función onAddToCart con el producto, tamaño, color y cantidad seleccionada
            onAddToCart({ ...product, selectedSize, quantity: parseInt(quantity, 10) });
        } else {
            // Muestra la notificación
            setShowNotification(true);
        }
    };
    const closeNotification = () => {
        // Cierra la notificación
        setShowNotification(false);
    };
    const handleImageClick = () => {
        setIsEnlarged(!isEnlarged);
    };

    const handleCloseClick = () => {
        setIsEnlarged(false);
        
    };

    return (
        <div className={`product-card ${isAdded ? 'added' : ''} ${isEnlarged ? 'enlarged' : ''}`}>
            <div className='img-product'>
                <img
                src={product.image}
                alt={product.name}
                onClick={handleImageClick}
                className={isEnlarged ? 'enlarged' : ''}
                loading="lazy"  
            />

            </div>
            {isEnlarged && (
                <div className="enlarged-overlay" onClick={handleCloseClick}>
                    <div className="enlarged-container">
                        <img src={product.image} alt={product.name} className="enlarged-image" />
                    </div>
                </div>
            )}
            <h3>{product.name}</h3>
            <p className='description'>{product.description}</p>
            <p className='pricee'>$ {product.price}</p>
            <div className='tyc'>
                <div className='talla'>
                    <label htmlFor={`size-${product.id}`}>Talle: </label>
                    <select
                        id={`size-${product.id}`}
                        name={`size-${product.id}`}
                        value={selectedSize}
                        onChange={(e) => setSelectedSize(e.target.value)}
                        disabled={isEnlarged}
                    >
                        {product.sizes.map((size) => (
                            <option key={size} value={size}>
                                {size}
                            </option>
                        ))}
                    </select>
                </div>
                <div className='cantidad'>
                    <label className='cant' htmlFor={`quantity-${product.id}`}>Cantidad: </label>
                    <input
                        type="number"
                        id={`quantity-${product.id}`}
                        name={`quantity-${product.id}`}
                        min="1"
                        value={quantity}
                        onChange={(e) => setQuantity(e.target.value)}
                        disabled={isEnlarged}
                    />
                </div>
            </div>
            <button className='add-car' onClick={handleAddToCart} disabled={isAdded || isEnlarged}>
                {isAdded ? 'Agregado al carrito' : 'Agregar al carrito'}
            </button>
            {showNotification && (
                <Notification message="Selecciona un talle antes de agregar al carrito." onClose={closeNotification} />
            )}
        </div>
    );
};

export default ProductCard;