import React, { useState } from 'react';
import ProductCard from './ProductCard';
import '../css/ProductsList.css';

const ProductsList = ({ products, onAddToCart, selectedCategory, selectedTalla }) => {
    const [visibleProducts, setVisibleProducts] = useState(8);
    const itemsToShow = products.slice(0, visibleProducts);

    const handleLoadMore = () => {
        setVisibleProducts(prevVisibleProducts => prevVisibleProducts + 8);
    };

    return (
        <div className="products-list-container">
            <ul className="products-list">
                {itemsToShow.map((product, index) => {
                    if (
                        (!selectedCategory || product.category === selectedCategory) &&
                        (!selectedTalla || product.sizes.includes(selectedTalla))
                    ) {
                        return (
                            <li key={product.id} className="product-card-container">
                                <ProductCard product={product} onAddToCart={onAddToCart} />
                            </li>
                        );
                    } else {
                        return null;
                    }
                })}
            </ul>
            <div className='btn-cargar'>
            {visibleProducts < products.length && (
                <button className="load-more-button" onClick={handleLoadMore}>
                    Ver más productos
                </button>
            )}
            </div>
        </div>
    );
};

export default ProductsList;